<template>
    <div>
        <b-list-group flush style="overflow:scroll;height:680px;">
                <b-list-group-item v-for="(item, index) in this.store.state.orderHistory" :key="index">
                    <div>
                        <b-btn v-b-modal="`modal${index}`" class="bg-transparent text-dark border-0 w-100"
                        v-on:click="getDetail(item.id)">
                            <div style="width:75%;height:100%;float:left;text-align:left;font-weight:bold">
                                {{item.customerBname}}
                                <br>
                                <span style="width:65%;font-weight:normal;">{{item.address1 + item.address2}}</span>
                            </div>
                            <div style="width:25%;float:right;">
                                <span style="font-weight:bold;" v-bind:style="{color: styleColor(item.orderstate)}">{{item.orderstate}}</span>
                            </div>
                        </b-btn>
                        <b-modal :id="`modal${index}`" :title="''"
                                 :ok-title="''"
                                 :modal-ok="true"
                                 :header-bg-variant="'success'"
                                 :header-text-variant="'white'"
                                 :ok-only="true">
                            <div class="w-100 bg-light" style="margin-top:10px;">
                                <b-list-group class="border-0">
                                    <b-list-group-item class="border-0 bg-light" style="font-weight:bold;">
                                        {{item.orderdate}}({{item.id}})<br>
                                         {{item.customerBname}}
                                    </b-list-group-item>
                                    <b-list-group-item v-for="(detailItem, index) in orderDetail" v-bind:key="index">
                                           <div style="width:10%;float:left;height:100%;text-align:center;">
                                                <b-form-checkbox v-model="detailItem.check" @change="addList(index);">
                                                </b-form-checkbox>
                                           </div>
                                           <div style="width:60%;height:100%;float:left;text-align:left;">
                                               <span style="font-weight:bold;">[{{detailItem.item.manufacturer}}] {{detailItem.item.itemName}}</span><br>
                                               <span style="font-size:0.9em;color:rgba(190,190,190,0.7);">
                                                   {{detailItem.item.origin}} | {{detailItem.item.unit}}
                                               </span>
                                           </div>
                                           <div style="width:10%;height:100%;float:left;font-weight:bold;">
                                               {{detailItem.orderitem.qty}}
                                           </div>
                                           <div style="width:20%;height:100%;float:right;font-weight:bold;" >
                                               <span v-bind:style="{ color: styleColor(detailItem.orderitem.state)}">{{detailItem.orderitem.state}}</span>
                                           </div>
                                    </b-list-group-item>
                                    <b-list-group-item class="border-0 bg-light" style="font-weight:bold;">
                                        <div>
                                            <b-form-textarea id="textarea1"
                                                v-model="detailText"
                                                placeholder=""
                                                :rows="3"
                                                :max-rows="6">
                                            </b-form-textarea>
                                        </div>
                                    </b-list-group-item>
                                    <b-list-group-item class="border-0 bg-light" style="font-weight:bold;">
                                        <div class="w-100">
                                            <span style="width:65%;float:left;font-weight:bold;"> </span>
                                            <span style="float:right;font-weight:bold;">{{nwc(detailQty)}} </span>
                                        </div>
                                        <div class="w-100">
                                            <span style="width:65%;float:left;font-weight:bold;"> </span>
                                            <span style="float:right;font-weight:bold;">{{nwc(calRefundTotal())}} </span>
                                        </div>
                                    </b-list-group-item>
                                    <b-list-group-item class="border-0 bg-light" style="font-weight:bold;">
                                        <div class="w-100 text-center">
                                            <b-button class="btn border-0" style="background:lightgray;" @click="reqFailure(item.id)"></b-button>
                                            <b-button class="btn border-0" style="background:rgb(31, 161, 133);" @click="reqSuccess(item.id)"></b-button>
                                        </div>
                                    </b-list-group-item>
                                </b-list-group>    
                            </div>   
                        </b-modal>
                    </div>
            </b-list-group-item>
        </b-list-group>
        <div class="text-center">
            <b-button class="btn border-0" style="width:90%;background:rgb(31, 161, 133);"></b-button>
        </div>
    </div>
</template>

<script>
    var baseurl="http://freshntech.cafe24.com";
    export default {
        name: 'OrderHistoryList',
        data () {
            return {
                orderDetail: [],
                detailTotal: 0,
                detailQty: 0,
                detailText: ''
            }
        },
        methods: {
            nwc(x) {
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            styleColor(idx) {
                switch(idx){
                    case '':
                        return 'black';
                        break;
                    case '':
                        return 'red';
                        break;
                    case '':
                        return 'blue';
                        break;
                    case '':
                        return 'green';
                        break;
                    case '':
                        return 'red';
                        break;
                    case '':
                        return 'lightgray';
                        break;
                    case '':
                        return 'lightgreen';
                        break;
                    case '':
                    case '':
                        return 'pink';
                        break;
                    case '':
                        return 'purple';
                        break;
                }
            },
            getDetail(id){
                console.log(id);
                this.$http.get(baseurl + '/orderdetail/' + id)
                .then((res) => {
                    this.orderDetail = res.data;
                    this.detailQty=0;
                    for(var i=0; i<this.orderDetail.length; i++){
                        this.detailQty+=Number(this.orderDetail[i].orderitem.qty);
                        this.orderDetail.check = false;
                        this.$forceUpdate();
                    }
                    console.log(this.orderDetail);
                })
                .catch((err) => {
                    console.log(err);
                })
            },
            addList(idx){
                if(this.orderDetail[idx].check){
                    this.orderDetail[idx].check = false;
                }else{
                    this.orderDetail[idx].check = true;
                }
                this.$forceUpdate();
            },
            calRefundTotal(){
                var qty= 0;
                for(var i=0; i < this.orderDetail.length; i++){
                    if(this.orderDetail[i].check === true){
                        qty+=this.orderDetail[i].orderitem.qty
                    }
                }
                return qty;
            },
            reqSuccess(id) {
                var test = [];
                var orderState ="";
                for(var i=0; i < this.orderDetail.length; i++){
                    if(this.orderDetail[i].check === true){
                        test.push({orderItemId: this.orderDetail[i].orderitem.id, orderItemState: ''})
                    }
                }
                if(test.length === 0){
                    alert('  .')
                    return;
                }
                var t = test.filter((item) => {
                    return item.orderstate === "";
                })
                if(t.length !== 0){
                    orderState = ""
                }else if(this.orderDetail.length > test.length){
                    orderState = ""
                }else{
                    orderState = ""
                }
                var postData = {
                    orderItemList: test,
                    orderId: id,
                    orderState: orderState
                }
                console.log(postData);
                this.$http.put(baseurl + '/orderdetail', postData)
                .then((res) => {
                    if(res.data === true){
                        alert(' .');
                        this.$router.push('/')
                    }
                })
            },
            reqFailure(id) {
                var test = [];
                for(var i=0; i < this.orderDetail.length; i++){
                    if(this.orderDetail[i].check === true){
                        test.push({orderItemId: this.orderDetail[i].orderitem.id, orderItemState: ''})
                    }
                }
                console.log(test);

                if(test.length === 0){
                    alert('  .')
                    return;
                }
                var postData = {
                    orderItemList: test,
                    orderId: id,
                    orderState: ""
                }
                this.$http.put(baseurl + '/orderdetail', postData)
                .then((res) => {
                    if(res.data === true){
                        alert(' .');
                        this.$router.push('/')
                    }
                })
                .catch((res) => {
                    console.log(res);
                })         
            }
            
        },
        created() {
            
        },
    }
</script>

<style scoped>

</style>


